<template>
  <div class="contain_block">
    <slot name="title"></slot>
    <div>
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="渠道">
          <el-select v-model="form.channel" placeholder="请选择渠道">
            <el-option
              v-for="(item, index) in channels"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="站点">
          <el-select
            @change="changeStore"
            v-model="form.store"
            placeholder="请选择站点"
          >
            <el-option
              v-for="(item, index) in selectOpts"
              :key="index"
              :label="item.store"
              :value="item.store"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="国家">
          <el-select v-model="form.country" placeholder="请选择国家">
            <el-option
              v-for="(item, index) in countries"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <div class="picker">
          <label>时间</label>
          <el-date-picker
            :disabled="dayDisabled"
            @change="change('日期')"
            v-model="form.dayPick"
            type="date"
            :picker-options="day_pickerOptions"
            placeholder="选择日期"
            value-format="yyyy-MM-dd"
          >
          </el-date-picker>
        </div>
        <div class="picker">
          <label>月份</label>
          <el-date-picker
            :disabled="monthDisabled"
            @change="change('月份')"
            format="yyyy 年 MM 月"
            value-format="yyyy-MM"
            v-model="form.monthPick"
            type="month"
            :picker-options="month_pickerOptions"
            placeholder="选择月份"
          >
          </el-date-picker>
        </div>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">生成</el-button>
        </el-form-item>
      </el-form>
      <h3 v-show="showGenarateTable">生成结果</h3>
      <div v-show="showGenarateTable">
        <!-- <table>
          <thead>
            <tr>
              <td>序号</td>
              <td>渠道</td>
              <td>站点</td>
              <td>国家</td>
              <td>时间</td>
              <td></td>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(items, index) in resultData" :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ items.channel }}</td>
              <td>{{ items.store }}</td>
              <td>{{ items.country }}</td>
              <td>{{ items.date }}</td>
              <td>
                <span>
                  下载
                  <a
                    class="download"
                    :href="
                      _url3 +
                        'order_report_download/?channel=' +
                        items.channel +
                        '&store=' +
                        items.store +
                        '&country=' +
                        items.country +
                        '&date=' +
                        items.date +
                        '&type=report'
                    "
                    :download="items.file_name"
                  ></a>
                </span>
              </td>
            </tr>
          </tbody>
        </table> -->
        <el-table
          border
          ref="singleTable"
          :data="resultData"
          highlight-current-row
          style="width: 100%"
        >
          <el-table-column type="index" width="50" label="序号">
          </el-table-column>
          <el-table-column property="channel" label="渠道" width="120">
          </el-table-column>
          <el-table-column property="store" label="站点" width="120">
          </el-table-column>
          <el-table-column property="country" label="国家"> </el-table-column>
          <el-table-column property="date" label="时间"> </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <!-- <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)"
                >{{ scope.row }}</el-button
              > -->
              <a
                class="download"
                :href="
                  _url3 +
                    'order_report_download/?channel=' +
                    scope.row.channel +
                    '&store=' +
                    scope.row.store +
                    '&country=' +
                    scope.row.country +
                    '&date=' +
                    scope.row.date +
                    '&type=report'
                "
                :download="scope.row.file_name"
                >下载</a
              >
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
// import axios from 'axios'
export default {
  data() {
    return {
      countries: [],

      // countries:['A','B','C'],
      channels: ["Amazon"],
      getdays: ["2020-07-01"],

      // 取到的数据
      selectOpts: [],
      // 生成结果 channel 渠道
      // store 站点
      // country 国家
      // date 时间
      resultData: [],
      // 默认不显示生成表格
      showGenarateTable: false,

      monthDisabled: false,
      dayDisabled: false,
      form: {
        channel: "",
        store: "",
        country: "",
        dayPick: "",
        monthPick: ""
      },
      // 月份
      monthList: "",
      month_pickerOptions: {
        disabledDate: function(time) {
          return time.getTime() > new Date().getTime();
        }
      },
      day_pickerOptions: {
        disabledDate: function(time) {
          return time.getTime() > new Date().getTime() - 8.64e7;
        }
      }
    };
  },
  props: ["type"],
  created() {
    // 请求数据：
    // this.$api3.getDropDown().then(val=>{
    //   console.log(val);
    // })
    // 请求路径后加斜杆！！！！
    console.log(this._url2);
    this.$axios
      .get(this._url2 + "server/databases/get_store_country/")
      .then(val => {
        console.log(val);
        this.selectOpts = val.data.data;
        console.log(this.selectOpts);
      });
  },
  methods: {
    onSubmit() {
      if (!this.form.channel || !this.form.store || !this.form.country) {
        alert("请选择选项");
        return;
      }
      if (!this.form.dayPick && !this.form.monthPick) {
        alert("请选择选项");
        return;
      }
      this.monthDisabled = false;
      this.dayDisabled = false;
      console.log(this.form);
      // 发起请求
      this.$axios
        .get(this._url2 + "server/databases/get_all_order/", {
          params: {
            channel: this.form.channel,
            store: this.form.store,
            country: this.form.country,
            date: this.form.dayPick || this.form.monthPick,
            // 不同的点通过prop
            // type: "completed_report"
            type: this.type
          }
        })
        .then(val => {
          // 获取请求，渲染生成结果
          console.log(val.data.data);
          this.resultData = val.data.data;
        });
      // 还原设置
      this.form = {};
      // 显示生成表格
      this.showGenarateTable = true;
    },
    change(val) {
      console.log(val);
      console.log("我改变啦");
      if (val === "日期") {
        this.monthDisabled = true;
      } else {
        this.dayDisabled = true;
      }
    },
    changeStore(val) {
      this.countries = [];
      for (let key in this.selectOpts) {
        const item = this.selectOpts[key];
        console.log(val);
        if (item.store === val) {
          this.countries = item.countries;
        }
      }
    },
    handleEdit(val1, val2) {
      console.log(val1, val2);
    }
    // downloadFile(index) {
    //   // 下载文件，发送get请求到后台
    //   console.log(index);
    //   console.log(this.resultData[index]);
    //   // 遍历 使用get 拼接到url
    //   const curData = this.resultData[index];
    //   const params = {
    //     channel: curData.channel,
    //     store: curData.store,
    //     country: curData.country,
    //     date: curData.date,
    //     // 不同的点
    //     // type: "completed_report"
    //     type: this.type
    //   };

    //   // 发送get请求
    //   // order_report_download/
    //   this.$axios
    //     .get(this._url2 + "download_order_report/", {
    //       params
    //     })
    //     .then(val => {
    //       console.log(val);
    //     });
    // }
  }
};
</script>

<style scoped>
.contain_block {
  width: 80%;
  margin: 30px auto;
}
h2 {
}
label {
  font-size: 14px;
  width: 80px;
  text-align: right;
}
table {
  border-collapse: collapse;
}
td,
th {
  border: 1px solid #e5e5e5;
  text-align: center;
  padding: 10px;
}
.contain_block span {
  position: relative;
  display: inline-block;
  background: lightblue;
  border-radius: 7px;
  font-size: 16px;
  width: 60px;
  color: #fff;
}
.contain_block .download {
  display: inline-block;
  width: 100%;
  height: 100%;
  text-align: center;
}
.dayPicker {
  margin-left: 31px;
  border: 1px solid #e4e4e4;
  position: relative;
  width: 221px;
  font-size: 15px;
  cursor: pointer;
}
.dayPicker span {
  font-size: 14px;
  width: 188px;
  line-height: 40px;
  background: transparent;
  color: #bbb;
}
.dayPicker_model {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  opacity: 0;
  margin-left: -3px;
  cursor: pointer;
}
h3 {
  margin: 40px 0;
}
h3 + div {
  padding: 20px 20vw 50px;
}
</style>
<style>
.contain_block .el-popper[x-placement^="bottom"] {
  margin-top: 40px;
}
.contain_block .el-form-item,
.contain_block .picker {
  width: 300px;
  margin: 0 auto 22px;
  position: relative;
}
.contain_block .el-form-item__label {
  text-align: center;
}
.contain_block .picker > label {
  width: 80px;
  padding: 0 12px 0 0;
  text-align: center;
  font-size: 14px;
  position: absolute;
  line-height: 40px;
}
.contain_block .picker > div {
  margin-left: 80px;
}
.contain_block .el-form {
  border-bottom: 1px solid #e5e5e5;
  padding-bottom: 40px;
}
.contain_block .el-button.el-button--primary {
  width: 100%;
}
.contain_block .el-table .cell {
  text-align: center;
}
</style>



