<template>
  <order-report-com type="report">
    <h2 slot="title">历史订单报告</h2>
  </order-report-com>
</template>

<script>
// import axios from 'axios'
import OrderReportCom from "@/components/dataBase/OrderReportCom";
export default {
  components: {
    OrderReportCom
  }
};
</script>
<style scoped>
.contain_block {
  width: 80%;
  margin: 30px auto;
  border: 1px solid #e5e5e5;
  border-radius: 10px;
  text-align: center;
}
h2 {
  margin: 3vw 0;
}
label {
  font-size: 14px;
  width: 80px;
  text-align: right;
}
table {
  border-collapse: collapse;
}
td,
th {
  border: 1px solid #e5e5e5;
  text-align: center;
  padding: 10px;
}
.contain_block span {
  position: relative;
  display: inline-block;
  background: lightblue;
  border-radius: 7px;
  font-size: 16px;
  width: 60px;
  color: #fff;
}
.contain_block .download {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
}
.dayPicker {
  margin-left: 31px;
  border: 1px solid #e4e4e4;
  position: relative;
  width: 221px;
  font-size: 15px;
  cursor: pointer;
}
.dayPicker span {
  font-size: 14px;
  width: 188px;
  line-height: 40px;
  background: transparent;
  color: #bbb;
}
.dayPicker_model {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  opacity: 0;
  margin-left: -3px;
  cursor: pointer;
}
</style>
<style></style>



